import React, { useContext, useEffect, useState } from 'react';
import { createToken, getPayload, etherTransaction, confirmTransaction } from '../Services/api';
import { utils } from 'web3';
import AppContext from '../Context/AppContext';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form'
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';

import Tab from './Tab'

const inputLabel = {
  paddingBottom: '20px',
  paddingTop: '15px',
};

const formGroupStyle = {
  display: 'flex',
  alignItems: 'flex-end',
  justifyContent: 'center',
};

function Tab3() {
  const {
    setKey,
    ethereum,
    accounts,
    tokenData,
    setTokenData,
    setJwt,
    jwt,
  } = useContext(AppContext);

  const [payment, setPayment] = useState(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    try {
      const { payment: p } = getPayload(jwt);
      setPayment(p);
    } catch {
      setPayment(null);
    }
  }, [jwt]);

  const handleChange = async ({ target }) => {
    if (!target.value) return;
    try{
      const { data: { token } } = await createToken({ ...tokenData, owner: target.value });
      setTokenData({ ...tokenData, owner: target.value });
      setJwt(token);
    } catch ({ message }) {
      alert(message);
    };
  }

  const backPage = () => {
    setKey('attributes');
  };

  const confirmPayment = async () => {
    const value = utils.toWei(payment.amount);
    const params = [
      {
        from: tokenData.owner,
        to: payment.address,
        value: utils.toHex(value),
      },
    ];
    const hash = await etherTransaction(ethereum, 'eth_sendTransaction', params);
    setLoading(true);
    try {
      await confirmTransaction(ethereum, utils.toHex(hash));
      setTokenData({ ...tokenData, hash });
      setKey('confirmation');
    } catch ({ message }) {
      alert(message);
    }
  };

  if (loading) return (
    <Tab>
      <div style={{ textAlign: 'center', paddingBottom: '16px' }}>
        <h3>DO NOT REFRESH OR LEAVE THIS PAGE!</h3>
        <p>Refreshing or leaving the page will invalid contract deployment</p>
        <div className="spinner-border" role="status">
          <span className="sr-only">Loading...</span>
        </div>
      </div>
    </Tab>
  );

  return (
    <Tab>
      <Form.Group
        as={Row}
        className="text-center"
        style={{ paddingTop: '20px' }}
      >
        <Col className="text-center" md={12} >
          <Form.Group as={Col} style={formGroupStyle}>
            <div style={{ width: '60%' }}>
              <Form.Label
                column="true"
                htmlFor="wallet_select"
                style={inputLabel}>
                  Wallet to deploy
              </Form.Label>
              <Form.Control
                as="select"
                id="wallet_select"
                defaultValue=""
                onChange={ handleChange }
              >
                <option value="">Choose Wallet...</option>
                { accounts.map((acc, i) => <option key={i}>{ acc }</option>) }
              </Form.Control>
            </div>
          </Form.Group>
        </Col>
      </Form.Group>
      {
        payment ?
          <Form.Group
            as={Row}
            className="text-center"
            style={{ paddingTop: '20px' }}
          >
            <Col className="text-center" md={12} >
                <p>
                  O valor total para a criação do seu token é: <span>{`${payment.amount} eth`}</span>.
                  Clique no botão abaixo para confirmar a transação
                </p>
                <Button
                  variant="success"
                  column="true"
                  type="button"
                  onClick={confirmPayment}>
                    Start Payment
                </Button>
            </Col>
          </Form.Group> : ''
      }
      <Form.Group as={Row} controlId="formHorizontal" className="text-center mt-4 mb-4">
        <Col sm={12} className="text-center mt-4 mb-4">
          <Button variant="danger" column="true" type="button" onClick={ backPage }>Back</Button>
        </Col>
      </Form.Group>
    </Tab>
  )
}

export default Tab3