import Axios from 'axios';
const URL = 'https://rei-btc.herokuapp.com/';

export const getPayload = (token) => {
  if(token === '') return;
  try {
    const { payload } = JSON.parse(atob(token.split('.')[1]));
    return payload;
  } catch ({ message }) {
    alert(message);
  }
};

export const createToken = async (tokenData) => {
  try {
    const { owner, name, symbol, supply } = tokenData;
    if (!/^0x[a-fA-F0-9]{40}$/.test(owner)) throw new Error('Invalid address format');
    if (name.length < 3 || name.length > 25) throw new Error('Invalid name length');
    if (symbol.length < 3 || symbol.length > 4) throw new Error('Invalid symbol length');
    if (supply < 1 || supply > 1000000000000000) throw new Error('Invalid supply');
    return await Axios.post(URL, tokenData);
  } catch ({ response: { data } }) {
    console.log(data);
    throw new Error(data.message);
  }
};

export const deployToken = async ({ hash }, jwt) => {
  const headers = {
    'Content-Type': 'application/json',
    'Authorization': jwt,
  };
  try {
    const { data } = await Axios.post(`${URL}payment`, { hash }, { headers });
    return data;
  } catch ({ response: { data } }) {
    throw new Error(data.message);
  }
};

export const deploy = async (jwt) => {
  const headers = {
    'Content-Type': 'application/json',
    'Authorization': jwt,
  };
  try {
    const { data } = await Axios.post(`${URL}confirm`, {}, { headers });
    return data;
  } catch ({ response }) {
    console.log(response);
    throw new Error(response.data.message);
  }
};

export const etherTransaction = async (ethereum, method, params) => {
  return new Promise((resolve, reject) => {
    ethereum.request({ method, params })
    .then((response) => {
      resolve(response);
    })
    .catch((error) => {
      console.error(error);
      reject();
    });
  })
};

export const confirmTransaction = async (ethereum, hash) => {
  return new Promise(async (resolve, reject) => {
    const intr = setInterval(async () => {
      try {
        const { blockHash } = await etherTransaction(ethereum, 'eth_getTransactionByHash', [hash]);
        if (blockHash !== null) {
          clearInterval(intr);
          resolve(blockHash);
        }
      } catch {
        clearInterval(intr);
        reject();
      };
    }, 3000);
  });
};
